.movie-view {
    padding: 2em;
    display: flex;
    justify-content: center;
    color: white;
    margin: 0 auto;
    max-width: 900px;
    // height: 100vh;
}
.movie-view a {
    width: 50px;
    height: 10px;
    margin: 1.2em 2em;
}
.movie-poster {
    display: block;
    padding: 0;
    margin: 0;
}
.col-left {
    margin: 0 auto;
}
.col-right {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    // font-size: 1.4em;
    padding-top: 0;
    padding-left: 0;
}
.label {
    color: grey;
    font-size: 1em;
    margin-right: .5em;
    text-transform: uppercase;;
}
.col-right .value {
    font-size: 1.2em;
    color: grey;
}
.movie-title .value {
    font-size: 1.7em;
    font-weight: bold;
    color: white;
}
span.label {
    position: relative;
    // padding: .2em;
}
span.label:before {
    content:'';
    position: absolute;
    background: ivory;
    opacity: .2;
    width: 100%;
    height: 92%;
    top: 3px;
    left: -2px;
}
.movie-title, .movie-genre, .genre-description, .genre-director {
    padding-top: 1em;
    color: grey;
}
.movie-view button.btn.btn-primary { 
     color: black;
     margin-top: 1.2em;
     font-weight: bold;
     background: tomato;
     outline: none;
     border-color: tomato;
     box-shadow: none;
}

@media all and (max-width: 450px) {
    .movie-view {
        display: block;
    }
}